import React, { useEffect, useRef, useState } from "react";
import { Panel, FlexboxGrid, IconButton } from "rsuite";
import { X, Bell } from "lucide-react";
import "./queueStyles.css"; // Importation des styles externalisés

function NotificationBar({ nextPatient, setShowNotification }) {
  // Référence à l'objet de synthèse vocale
  const synth = useRef(null);
  const voiceRef = useRef(null);
  const [voicesLoaded, setVoicesLoaded] = useState(false);

  // Initialiser la synthèse vocale immédiatement au montage du composant
  useEffect(() => {
    if (typeof window !== "undefined" && window.speechSynthesis) {
      synth.current = window.speechSynthesis;

      // Charger les voix disponibles
      const loadVoices = () => {
        const availableVoices = synth.current.getVoices();

        if (availableVoices.length > 0) {
          // Rechercher d'abord une voix Google française
          let voiceToUse = availableVoices.find(
            (v) =>
              v.name.toLowerCase().includes("google") && v.lang.includes("fr")
          );

          // Si aucune voix Google française n'est trouvée, sélectionner une voix française quelconque
          if (!voiceToUse) {
            voiceToUse = availableVoices.find((v) => v.lang.includes("fr"));
          }

          // Si toujours aucune voix française, prendre la voix par défaut
          if (!voiceToUse && availableVoices.length > 0) {
            voiceToUse = availableVoices[0];
          }

          if (voiceToUse) {
            voiceRef.current = voiceToUse;
            console.log("Voix sélectionnée:", voiceToUse.name);
            setVoicesLoaded(true); // Marquer les voix comme chargées
          }
        }
      };

      // Firefox et certains navigateurs peuvent avoir les voix déjà chargées
      loadVoices();

      // Chrome et autres navigateurs nécessitent d'attendre l'événement voiceschanged
      synth.current.onvoiceschanged = () => {
        loadVoices();
        // Assurer que les voix sont marquées comme chargées
        setVoicesLoaded(true);
      };

      // Initialiser les voix même si onvoiceschanged n'est pas déclenché
      if (synth.current.getVoices().length > 0) {
        setVoicesLoaded(true);
      }
    }

    return () => {
      if (synth.current) {
        synth.current.onvoiceschanged = null;
        synth.current.cancel(); // Arrêter la synthèse vocale si le composant est démonté
      }
    };
  }, []);

  // Fonction pour lire le texte du ticket
  const speakTicketInfo = () => {
    if (!synth.current) {
      console.error("La synthèse vocale n'est pas disponible");
      return;
    }

    // Arrêter toute lecture en cours
    synth.current.cancel();

    try {
      // Créer un texte à partir des informations du ticket
      const textToSpeak = ` Le ticket numéro ${nextPatient.STR_TICNAME}. pour une opération de ${nextPatient.LG_LSTOPEID}. est appelé a la caisse numéro 1`;

      // Créer un nouvel objet SpeechSynthesisUtterance
      const utterance = new SpeechSynthesisUtterance(textToSpeak);

      // Définir la langue
      utterance.lang = "fr-FR";

      // Ralentir la vitesse de lecture
      utterance.rate = 0.9;

      // Définir la voix si une est sélectionnée
      if (voiceRef.current) {
        utterance.voice = voiceRef.current;
      }

      // Événements pour le débogage
      utterance.onstart = () => console.log("Début de l'annonce vocale");
      utterance.onend = () => console.log("Fin de l'annonce vocale");
      utterance.onerror = (e) => console.error("Erreur d'annonce vocale:", e);

      // Lire le texte
      synth.current.speak(utterance);
    } catch (error) {
      console.error("Erreur lors de la lecture vocale:", error);
    }
  };

  // Effectuer les lectures vocales dès que le composant est monté et que les voix sont chargées
  useEffect(() => {
    if (nextPatient && voicesLoaded) {
      // Première lecture immédiate dès que les voix sont chargées
      speakTicketInfo();

      // Deuxième lecture après 10 secondes
      const timer = setTimeout(() => {
        speakTicketInfo();
      }, 10000);

      return () => {
        clearTimeout(timer);
        if (synth.current) {
          synth.current.cancel(); // Arrêter la synthèse vocale si le composant est démonté
        }
      };
    }
  }, [nextPatient, voicesLoaded]); // Dépendance à voicesLoaded pour s'assurer que les voix sont prêtes

  return (
    <Panel bordered className="notification-bar">
      <FlexboxGrid justify="space-between" align="middle" className="mb-10">
        <FlexboxGrid.Item>
          <h4>Prochain patient</h4>
        </FlexboxGrid.Item>
        <FlexboxGrid.Item>
          <IconButton
            icon={<X size={16} />}
            size="xs"
            appearance="subtle"
            onClick={() => setShowNotification(false)}
          />
        </FlexboxGrid.Item>
      </FlexboxGrid>
      <FlexboxGrid align="middle">
        <FlexboxGrid.Item className="mr-15">
          <div className="notification-icon">
            <Bell size={18} color="#1c64f2" />
          </div>
        </FlexboxGrid.Item>
        <FlexboxGrid.Item>
          <p className="notification-name">{nextPatient.name}</p>
          <p className="notification-room">{nextPatient.room}</p>
        </FlexboxGrid.Item>
      </FlexboxGrid>
    </Panel>
  );
}

export default NotificationBar;
